/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
  color: #25282a;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 1.6;
}

blockquote {
  margin: 0 25px;
}

pre {
  max-width: 100%;
  padding: 1.6em;
  overflow: auto;
  background: #bebebe;
}

[class*='font-'] {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #000;
  font-size: 30px;
  line-height: 1;
}

/*--------------------------------------------------------------
## Base Layout
--------------------------------------------------------------*/
html, body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -ms-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  height: 100%;
}

html {
  box-sizing: border-box;
}

/*--------------------------------------------------------------
## Layout
--------------------------------------------------------------*/
.wrapper {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.wrapper-md {
  max-width: 943px;
}

.wrapper--sm {
  max-width: 550px;
}

/*--------------------------------------------------------------
## Animations
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Parts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Buttons
--------------------------------------------------------------*/
.btn {
  display: inline-block;
  padding: 15px 36px;
  transition: 550ms ease;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
  font-family: "Open Sans", sans-serif;
  color: #fff;
  background-color: #92e4e4;
  font-weight: bold;
}

.btn:hover {
  background-color: #535353;
}

.btn:active, .btn:focus {
  outline: none;
}

@media (max-width: 1024.98px) {
  .btn {
    padding: 7px 15px;
    font-size: 11px;
  }
}

.btn-lg {
  padding: 14px 0;
  max-width: 433px;
  width: 100%;
  display: block;
}

.btn-md {
  padding: 13px 35px;
}

@media screen and (max-width: 768px) {
  .btn-md {
    padding: 9px 21px;
  }
}

.btn-sm {
  padding: 7px 38px;
}

/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Forms
--------------------------------------------------------------*/
input[type='text'],
input[type='email'],
input[type='tel'],
input[type='date'] {
  max-width: 433px;
  width: 100%;
  outline: none;
  border: 1px solid #ddd;
  padding: 12px;
  font-family: "Open Sans", sans-serif;
  display: block;
  margin-bottom: 22px;
}

input[type='text']:hover,
input[type='email']:hover,
input[type='tel']:hover,
input[type='date']:hover {
  border-color: #000;
}

::placeholder {
  font-family: "Lato", sans-serif;
  font-size: 16px;
}

/*--------------------------------------------------------------
## Wrapper and Ratios
--------------------------------------------------------------*/
img {
  max-width: 100%;
  display: block;
  height: initial;
}

